.container{
    color: white;
    display: flex;
    justify-content: center;
}
.appDiv{
    font-family: 'Righteous', cursive;
    margin-top: 5vh;
    width: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}
h1{
    text-align: center;
}
.bAndS{
    display: flex;
    justify-content: space-between;
}
.timerP{
    margin: 0 0;
}
.enlargeText{
    font-size: 25px;
}
p{
    text-align: center;
}

button{
    color: white;
    background-color: rgb(14, 115, 155);
    border: none;
    width: 40px;
}
button:focus{
    outline: none;
}
button:active{
    outline: none;
}
.breakDiv, .sessionDiv{
    width: 225px;
}
.sessionItems, .breakItems{
    display: flex;
    justify-content: center;
}
.timerDiv{
    width: 50%;
    border: 4px solid darkslategrey;
    border-radius: 30px;
    align-items: center;
    display: flex;
    flex-direction: column;
    padding: 15px 15px;
}
.timerClock{
    font-family: 'Orbitron', sans-serif;
    font-size: 40px;
}
.regards{
    font-family: 'Share Tech', sans-serif;
    color: red;
}
.pauseResumeReset{
    display: flex;
    justify-content: center;
}

@media(max-width: 480px){
    .appDiv{
        margin-top: 0;
    }
    .bAndS{
        flex-direction: column;
        align-items: center;
    }
    h1{
        margin-top: 0;
        margin-bottom: 10px;
    }
    .enlargeText{
        margin: 0 0;
    }
    .breakDiv, .sessionDiv{
        width: 100%;
        margin: 0 0;
    }
}